#cards:hover{
    background-color: rgb(70, 200, 204);
    transform: scale(1.1);
    transition: all 0.5s ease-in-out;
    cursor: pointer;

}
#maxx{
    max-width: 100%;
    height: 10vh;
    margin-bottom: 20px;
    text-size-adjust: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}